export default {
	data() {
		return {
			visible: false,
			dirs: [
				{
					label: '顶部弹出',
					value: 'top',
					visible: false,
					size: 'auto'
				},
				{
					label: '右侧弹出',
					value: 'right',
					visible: false,
					size: 'auto'
				},
				{
					label: '底部弹出',
					value: 'bottom',
					visible: false,
					size: 'auto'
				},
				{
					label: '左侧弹出',
					value: 'left',
					visible: false,
					size: 'auto'
				},
				{
					label: '中间弹出',
					value: 'center',
					visible: false,
					size: 'auto'
				}
			]
		};
	},

	methods: {
		open(item) {
			item.visible = true;
		},
		submit() {
			this.$refs['confirm'].open({
				title: '提示',
				message: '确定添加该条数据吗？',
				callback: ({ action }) => {
					if (action === 'confirm') {
						this.visible = false;
					}
				}
			});
		}
	}
};
